@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.movie_card {
  border: 1px solid #ddd;
  border-radius: 10px;
  background-size: cover;
  background-position: center center;
  box-shadow: 0px 2px 20px -7px rgba(0, 0, 0, 0.5);
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.movie_card:hover {
  transform: translateY(-10px);
  box-shadow: 0px 8px 20px -7px rgba(0, 0, 0, 0.5);
  background-position: center right;
}
.left_side {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.left_side_inner {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 15px;
}
.left_side_inner > div {
  margin-left: 20px;
}
.left_side_inner > img {
  width: 150px;
  border-radius: 5px;
}
.duration {
  border: 1px solid #aaa;
  background-color: rgb(238, 238, 238);
  border-radius: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.description {
  margin-top: 10px;
  font-size: 14px;
  font-family: "Poppins", sans-serif;
}
.search {
  width: 100%;
  display: flex;
  align-items: center;
}
label {
  margin: 0 !important;
}
.search > input {
  border-radius: 5px;
  border: 1px solid #f1f1f1;
  padding: 10px;
  box-shadow: 0px 2px 15px -10px rgba(0, 0, 0, 0.5);
  width: 75%;
  outline: none;
}
.add_btn {
  font-size: 3rem;
}
.desc {
  display: flex !important;
  flex-direction: row !important;
  justify-content: center;
  align-items: center;
}

